@charset "UTF-8";

$backgroundColor: rgba(#000, 0.9);
$arrowWidth: 6px;
$arrowHalfWidth: 3px;
$arrowColor: $backgroundColor;

.bdp-tooltip {
	position: relative;

	.bdp-tooltip-container {
		color: #FFF;
		position: absolute;
		pointer-events: none;
		z-index: 850;
		width: 250px;
		opacity: 0;
		transition: opacity 0.1s, transform 0.1s;
		font-size: 14px;
		text-align: left;

		.bdp-tooltip-arrow {
			border: 6px solid;
			position: absolute;
			width: 0;
			height: 0;
		}

		.bdp-tooltip-content {
			background: $backgroundColor;
			display: inline-block;
			position: relative;
			padding: 2px 7px 3px 7px;
			border-radius: 3px;
			text-align: center;
		}

		// ==================================================================
		// =                             Normal                             =
		// ==================================================================
		// Top Tooltip
		&.top {
			bottom: 100%;
			left: 50%;
			transform: translate(-50%, -$arrowWidth) scale(0.8, 0.8) translate(0, 20%);

			.bdp-tooltip-arrow {
				border-color: $arrowColor transparent transparent transparent;
				top: 100%;
				left: 50%;
				margin-left: -$arrowWidth;
			}

			.bdp-tooltip-content {
				left: 50%;
				transform: translate(-50%, 0);
			}
		}

		// Right Tooltip
		&.right {
			left: 100%;
			top: 50%;
			transform: translate($arrowWidth, -50%) scale(0.8, 0.8) translate(-20%, 0);

			.bdp-tooltip-arrow {
				border-color: transparent $arrowColor transparent transparent;
				top: 50%;
				left: 0;
				margin-left: -$arrowWidth * 2;
				margin-top: -$arrowWidth;
			}
		}

		// Bottom Tooltip
		&.bottom {
			top: 100%;
			left: 50%;
			transform: translate(-50%, $arrowWidth) scale(0.8, 0.8) translate(0, -20%);

			.bdp-tooltip-arrow {
				border-color: transparent transparent $arrowColor transparent;
				bottom: 100%;
				left: 50%;
				margin-left: -$arrowWidth;
			}

			.bdp-tooltip-content {
				left: 50%;
				transform: translate(-50%, 0);
			}
		}

		// Left Tooltip
		&.left {
			right: 100%;
			top: 50%;
			transform: translate(-$arrowWidth, -50%) scale(0.8, 0.8) translate(20%, 0);
			text-align: right;

			.bdp-tooltip-arrow {
				border-color: transparent transparent transparent $arrowColor;
				top: 50%;
				right: -$arrowWidth * 2;
				margin-top: -$arrowWidth;
			}

			.bdp-tooltip-content {
				right: 0;
			}
		}

	}

	// =================================================================
	// =                             Hover                             =
	// =================================================================
	&:hover {
		.bdp-tooltip-container {
			opacity: 1;

			&.top {
				transform: translate(-50%, -$arrowWidth);
			}

			&.right {
				transform: translate($arrowWidth, -50%);
			}

			&.bottom {
				transform: translate(-50%, $arrowWidth);
			}

			&.left {
				transform: translate(-$arrowWidth, -50%);
			}
		}
	}
}



