@charset "UTF-8";

$buttonColor: #000;

.bdp-box {
	.box-header-tools {
		position: absolute;
		top: 0px;
		right: 7px;
		height: 38px;
		display: flex;
		align-items: center;

		.form-group {
			margin: 0;
		}

		button:not(.btn) {
			margin-top: 1px;
			background: transparent;
			padding: 5px;
			font-size: 12px;
			height: 25px;
			width: 25px;
			border: 0;
			color: rgba($buttonColor, 0.3);
			outline: none;

			&:hover {
				color: rgba($buttonColor, 0.5);
			}

			&:active {
				color: rgba($buttonColor, 0.8);
			}
		}

		button.btn {
			margin-top: 1px;
			padding: 0 5px;
			height: 25px;
			font-size: 12px;
		}

		button + button {
			margin-left: 3px;
		}

		input, select {
			width: initial;
			display: inline-block;
			padding: 0 5px;
			height: 28px;
		}

		btn, input, select {
			vertical-align: top;
		}
	}
}



